<template>
  <div style="width:100%;height:100%;display: flex;justify-content: center;align-items: center;">
    <div class="login-container">
    <h2 class="login-title">用户登录</h2>
    <el-form class="login-form" ref="loginForm" :model="loginForm" :rules="loginRules">
      <el-form-item prop="account">
        <h4 style="color:black">账号</h4>
        <el-input v-model="loginForm.account" placeholder="请输入账号"></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <h4 style="color:black">密码</h4>
        <el-input v-model="loginForm.password" placeholder="请输入密码" type="password" show-password></el-input>
      </el-form-item>
      <el-form-item class="captcha">
        <h4 style="color:black">验证码</h4>
        <div class="captcha-input-container">
          <el-input v-model="loginForm.code" placeholder="请输入验证码" class="captcha-input"></el-input>
          <img v-show="isCaptcha" :src="imageCode" @click="getCodeImage" alt="验证码" class="captcha-image" />
        </div>
      </el-form-item>
      <el-form-item class="login-buttons">
        <el-button type="primary" class="login-button" @click="login">登录</el-button>
      </el-form-item>
    </el-form>
    <div class="login-options">
      <div class="remember-account">
        <el-checkbox v-model="loginForm.remember">记住账号</el-checkbox>
      </div>
      <div class="register-link"><router-link to="/register" style="color:#0066cc">立即注册</router-link></div>
    </div>
  </div>
 </div>
</template>

<script>
import db from "@/utils/localstorage";
import {randomNum} from "@/utils/common";
// import {socialLoginUrl} from "@/settings";
import loginApi from "@/api/Login.js";
import {Base64} from "js-base64";
import { eventBus } from '@/main.js';
export default {
  data() {
    return {
      isCaptcha: process.env.VUE_APP_IS_CAPTCHA === "true",
      imageCode: "",
      loginForm: {
        account: 'zhitu', //"admin" "zhitu"
        password: '888888', //"123456" "888888"
        // captcha: '',
        remember: false,
        code: "",
        //add-s
        tenantView: "0000", //显示用的
        tenant: "", //传递给后端的
        key: randomNum(24, 16),
        grantType: process.env.VUE_APP_IS_CAPTCHA === "true" ? "captcha" : "password",
        bindAccount: "",
        bindPassword: "",
        signAccount: "",
        signPassword: "",
        //add-e
      },
      loginRules: {
        account: [
          { required: true, message: '请输入账号', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ],
        captcha: [
          { required: true, message: '请输入验证码', trigger: 'blur' }
        ]
      }
    };
  },
  methods: {
    login() {
      // 处理登录逻辑
      this.loginForm.tenant = `${Base64.encode(this.loginForm.tenantView)}`;
      const that = this;
      this.$store.commit("account/setTenant", this.loginForm.tenant);
      loginApi
        .login(this.loginForm, {tenant: this.loginForm.tenant, "X-isTenant": false})
        .then(response => {
          const res = response.data;
          if (res.isSuccess) {
            that.saveLoginData(
              res.data["token"],
              res.data["refreshToken"],
              res.data["expiration"]
            );
            that.saveUserInfo(res.data);
            this.$message({
              type: 'success',
              message: '登录成功'
            });
            this.$router.push('/home');
            eventBus.$emit('checkAvatar');
          } 
        })
        .catch((error) => {
          console.log(error)
        })
        
    },
    getCodeImage() {
      console.log("isCaptcha",this.isCaptcha)
      if (this.isCaptcha) {
        loginApi
          .getCaptcha(this.loginForm.key)
          .then(response => {
            const res = response.data;
            if (res.byteLength <= 100) {
              this.$message({
                message: this.$t("tips.systemError"),
                type: "error"
              });
            }
            return (
              "data:image/png;base64," +
              btoa(
                new Uint8Array(res).reduce(
                  (data, byte) => data + String.fromCharCode(byte),
                  ""
                )
              )
            );
          })
          .then(res => {
            this.imageCode = res;
          })
          .catch(e => {
            if (e.toString().indexOf("429") !== -1) {
              this.$message({
                message: this.$t("tips.tooManyRequest"),
                type: "error"
              });
            } else {
              this.$message({
                message: this.$t("tips.getCodeImageFailed"),
                type: "error"
              });
            }
          });
      }
    },
    saveLoginData(token, refreshToken, expiration) {
      this.$store.commit("account/setToken", token);
      this.$store.commit("account/setRefreshToken", refreshToken);
      this.$store.commit("account/setExpireTime", expiration);
    },
    saveUserInfo(user) {
      this.$store.commit("account/setUser", {
        id: user.userId,
        account: user.account,
        name: user.name,
        avatar: user.avatar,
        workDescribe: user.workDescribe
      });
    },
    keyDown(e) {
      if (e.keyCode === 13) {
        this.login()
      }
    }, 
  },
  mounted() {
    db.clear();
    this.getCodeImage();
    // 绑定监听事件
    window.addEventListener("keydown", this.keyDown);
  },
  destroyed() {
    // 销毁事件
    window.removeEventListener("keydown", this.keyDown, false);
  },
};
</script>

